import { graphql } from 'gatsby'

export const query = graphql`
  query MobileMenuQuery {
    allArticleCategory(
      filter: { hideCat: { ne: "true" } }
      sort: { catOrder: ASC }
    ) {
      nodes {
        catOrder
        hideCat
        name
        slug
      }
    }
    allArticleTag {
      nodes {
        id
        name
        slug
      }
    }
  }
`

const PageAuthors = props => {
  // console.log('PageAuthors props=', props)
  const catItems = props.data.allArticleCategory.nodes
  const tagItems = props.data.allArticleTag.nodes
  // console.log('PageContact catItems=', catItems, 'tagItems=', tagItems)
  const mobileMenu = []
    .concat(
      catItems && catItems.length > 0
        ? [
            {
              title: '主題',
              items: catItems
            }
          ]
        : []
    )
    .concat(
      tagItems && tagItems.length > 0
        ? [
            {
              title: '標籤',
              items: tagItems
            }
          ]
        : []
    )
  // console.log('PageContact, mobileMenu=', mobileMenu)
  props.pageContext.mobileMenu = mobileMenu

  // const authors = useBlogAuthors()

  return (
    // <Layout {...props}>
    //   <Seo title='團隊' />
    //   <Divider />
    //   <Stack effectProps={{ effect: 'fadeInDown' }}>
    //     <PageTitle
    //       header='Team Members'
    //       subheader='FlexiBlog theme comes with a pre-made contact form component. You can integrate this form with serverless services such as Formspree, Getform, FormKeep and others to receive form submissions via email.'
    //     />
    //   </Stack>
    //   <Stack>
    //     <Main>
    //       {authors.map((author, i) => (
    //         <React.Fragment key={`item-${i}`}>
    //           <Divider />
    //           <AuthorExpanded author={author} withLink />
    //         </React.Fragment>
    //       ))}
    //     </Main>
    //   </Stack>
    // </Layout>
    <Layout {...props}>
    </Layout>
  )
}

export default PageAuthors
